.App {
  padding-top: 40px;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  /* background-image: linear-gradient(
    to right top,
    #051937,
    #004d7a,
    #008793,
    #00bf72,
    #a8eb12
  ); */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

input[type='text'].Textbox {
  border: 2px solid #e3e3e3;
  border-radius: 0px;
  background: white;
  padding: 10px 10px;
}

input[type='text'].Textbox:focus {
  border: 2px solid #0078d4;
}

.switch.on {
  background: #0078d4;
  border: 2px solid #0078d4;
}

.switch.on .switch-toggle {
  left: calc(40px - 12px);
  background: white;
}

.switch {
  border: 2px solid #363636;
  padding: 3px;
  width: 40px;
  height: 14px;
  border-radius: 14px;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-right: 7px;
}

.rc-slider-track {
  background: #e9e9e9;
}

.switch-toggle {
  width: 12px;
  height: 12px;
  left: 0;
  border-radius: 50%;
  background: #363636;
  position: relative;
  -webkit-transition: left 0.2s ease-in-out;
  -o-transition: left 0.2s ease-in-out;
  transition: left 0.2s ease-in-out;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.container {
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
  font-size: 14px;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  margin: 10px 18px;
}

.checkbox {
  display: block;
}

.checked {
  color: #363636;
  font-weight: 500;
}

.checkmark {
  height: 18px;
  width: 18px;
  border-radius: 50%;
  border: 2px solid #363636;
  background: white;
  margin-right: 7px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.checkmark.checked {
  border: 2px solid #0078d4;
}

.checkmarkChecked {
  background: #363636;
  /* border: 2px solid #0078d4; */
  border-radius: 50%;
  height: 14px;
  width: 14px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


